<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <Table :columns="columns" :data="tableData">
        <template slot-scope="{ row }" slot="2">
          <span style="color:#538FFF">{{row['2']}}</span>
        </template>
        <template slot-scope="{ row }" slot="reason">
          <button @click="lookReason(row)">补正原因</button>
        </template>
        <template slot-scope="{ row }" slot="text">
         <a href="#" style="color:#538FFF">查看</a>
        </template>
        <template slot-scope="{row}" slot="action">
          <div>
            <button @click="handleSure(row)">申请补正通过</button>
            <button @click="handleBack(row)">申请补正驳回</button>
            <button @click="handleLook(row)">重新生成文书</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage"
        :total="total"
        :page-size="pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <!-- 申请补正通过 -->
    <make-official  v-bind:visbible.sync="avoidDilog" @successAvoid="avoidSubmit"/>
    <!-- 申请补正驳回提示 -->
    <make-official  v-bind:visbible.sync="backDilog" :disabledType="true" @successAvoid="backSubmit"/>
    
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue"
import makeOfficial from './compons/makeOfficial' // 生成文书
const columns = [
  { title: "案号", slot: "2" },
  { title: "申请人" },
  { title: "被申请人" },
  { title: "案件状态" },
  { title: "案件类型" },
  { title: "补正原因", slot: "reason" },
  { title: "文书驳回原因", slot: "text" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
  components: {
    listSearch,
    makeOfficial
  },
  data() {
    return {
      searchData: {
        number: "",
        appeal: "",
        regName: [],
        caseType: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "select",
          value: "regName",
          label: "注册名称",
          placeholder: "请选择注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      currentPage: 1,
      total: 0,
      pageSize: 10,
      columns: columns,
      tableData: [{ 1: 1, 2: 2 }],
      avoidDilog:false, // 补正通过
      backDilog:false, // 补正驳回
    };
  },
  methods: {
      goSearch(){},
      //  通过
      avoidSubmit(params){
          console.log(params,'提交参数params')
      },
      //   驳回
    backSubmit(params){
        console.log(params,'提交参数params')
    },
      handleSure(row){
          this.avoidDilog=true
      },
      handleBack(row){
        //   文书类型不可选，默认申请补正通过文书

          this.backDilog=true
      }
    
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
</style>


