<template>
  <Modal v-model="visbible" :footer-hide="true" title="生成文书" @on-cancel="cancel">
    <Form
      ref="avoidFormValidate"
      :model="avoidForm"
      :rules="avoidFormRuleValidate"
      :label-width="110"
    >
      <FormItem label="文书类型" prop="textType">
        <Select v-model="avoidForm.textType" :disabled="disabledType">
          <Option value="beijing">New York</Option>
          <Option value="shanghai">London</Option>
          <Option value="shenzhen">Sydney</Option>
        </Select>
      </FormItem>
      <FormItem label="文书来源" prop="source">
        <RadioGroup v-model="avoidForm.source">
          <Radio label="1">结案模板</Radio>
          <Radio label="2">文件上传</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="选择文书模板" prop="template" v-if="avoidForm.source==1|| avoidForm.source==''">
        <div style="display:flex;align-items:center;">
          <Select v-model="avoidForm.template">
            <Option value="beijing">裁决书</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
          <img
            style="margin-left:30px;cursor:pointer;"
            width="19"
            height="22"
            src="../../../assets/images/yulan.png"
            alt
          >
        </div>
      </FormItem>
      <FormItem label="材料附件" v-if="avoidForm.source==2">
        <div style="display:flex">
          <Upload action="//jsonplaceholder.typicode.com/posts/" :before-upload="handleUpload">
            <Button type="primary" size="small">浏览...</Button>
          </Upload>
          <div
            style="margin-left:10px;height: 40px;"
          >{{avoidForm.file? avoidForm.file.name: '未选择文件'}}</div>
        </div>
      </FormItem>
      <FormItem label="驳回原因" prop="backInput">
        <Input
          v-model="avoidForm.backInput"
          :maxlength="100"
          show-word-limit
          type="textarea"
          placeholder="请输入原因"
          style="width:100%"
        />
      </FormItem>
    </Form>
    <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
      <Button style="margin-right:20px;" @click="cancel">取消</Button>
      <Button type="primary" @click="avoidSubmit">确定</Button>
    </div>
  </Modal>
</template>
<script>
// 生成文书组件
export default {
  props: {
    visbible: {
      type: Boolean,
      default: false
    },
    // 文本类型
    disabledType:{
        type:Boolean,
        default:false
    }
  },
  data() {
    return {
      avoidForm: {
        textType: "",
        source: "",
        template: "",
        file: "",
        backInput: ""
      },
      avoidFormRuleValidate: {
        textType: [
          { required: true, message: "请选择类型", trigger: "change" }
        ],
        source: [{ required: true, message: "请选择来源", trigger: "change" }],
        template: [
          { required: true, message: "请选择模板", trigger: "change" }
        ],
        backInput: [
          { required: true, message: "请输入驳回原因", trigger: "change" }
        ]
      }
    };
  },
  methods: {
      cancel(){
          this.$emit('update:visbible',false)
      },
    // 回避驳回提交
    avoidSubmit() {
      this.$refs["avoidFormValidate"].validate(valid => {
        console.log(this.avoidForm);
        if (valid) {
          // 模板 1 模板2 文件
            this.avoidForm.source==1 ? this.avoidForm.file='':this.avoidForm.template=''
          this.$Message.success("操作成功");
          this.$emit("successAvoid", this.avoidForm);
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    handleUpload(file) {
      console.log(file);
      this.avoidForm.file = file;
      return false;
    }
  }
};
</script>

